* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow: none;
}

.navbar {
    width: 100%;
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5vw;
    background: #fff;
    z-index: 9;
}

.navbar>.logo {
    width: 50px;
    height: 50px;
    /* margin: -0.1em 0 0 1em; */
    margin-left: -1em;
}

.links-container {
    display: flex;
    list-style: none;
}

.link {
    padding: 10px;
    margin-left: 10px;
    text-decoration: none;
    text-transform: capitalize;
    color: #000;
}